import React, { useState } from "react";
import "./ClubCardsSection.css";
import bg from "../../assets/images/location/LocationBakground.jpg.webp";

const DOCS = [
  { text: " -    ", href: "#" },
  { text: " 1.   ", href: "#" },
  { text: " 3    ", href: "#" },
  { text: " 3     ", href: "#" },
  { text: " 4 -", href: "#" }
];

const ADULT_PLANS = [
  {
    id: "a1",
    label: "1 ",
    pricePerMonth: "  ",
    price: "9 500 ",
    perks: ["    ", " ", "", " ", " ", ": 50%  ", ", ,   "]
  },
  {
    id: "a3",
    label: "3 ",
    pricePerMonth: "  ",
    price: "7 666 ",
    perks: ["    ", "1  ", "   ()", "3    ", "2  ", "SPA   ", ", ,   "]
  },
  {
    id: "a6",
    label: "6 ",
    pricePerMonth: "  ",
    price: "6 333 ",
    perks: ["    ", " ", "3    ", "5  ", "", " ", ", ,   "]
  },
  {
    id: "a12",
    label: "12 ",
    pricePerMonth: "  ",
    price: "5 250 ",
    perks: ["    ", " ", "7    ", "10  ", " ", " ", "SPA, , , "]
  }
];

const KIDS_PLANS = [
  {
    id: "k3",
    label: "3 ",
    pricePerMonth: "  ",
    price: "1 833 ",
    perks: ["7  ", " ", " ", "  ", ", ,   ", "  1-2 ", " "]
  },
  {
    id: "k6",
    label: "6 ",
    pricePerMonth: "  ",
    price: "1 500 ",
    perks: ["15  ", " ", " ", "  ", ", ,   ", "  1-2 ", " "]
  },
  {
    id: "k12",
    label: "1 ",
    pricePerMonth: "  ",
    price: "1 042 ",
    perks: ["30  ", " ", " ", "  ", ", ,   ", "  1-2 ", " "]
  }
];

function DocumentLinks() {
  return (
    <div className="docs" style={{ maxWidth: 800, margin: "0 auto", textAlign: "center" }}>
      <p>    ,  ,   :</p>
      <ul style={{ listStyle: "none", padding: 0 }}>
        {DOCS.map((link, idx) => (
          <li key={idx} style={{ margin: "4px 0" }}>
            <a href={link.href}>{link.text}</a>
          </li>
        ))}
      </ul>
    </div>
  );
}

function PlanCard({ plan }) {
  return (
    <article className="plan">
      <div className="badge">
        <div className="badge-logo">UNI</div>
        <div className="badge-sub">! ! !</div>
      </div>
      <div className="plan-body">
        <div className="plan-meta">
          <div className="term">{plan.label}</div>
          <div className="ppm">{plan.pricePerMonth}</div>
          <div className="price">{plan.price}</div>
        </div>
        <ul className="perks">
          {plan.perks.map((perk, i) => <li key={i}>{perk}</li>)}
        </ul>
        <button className="buy"></button>
      </div>
    </article>
  );
}

function Tabs({ active, onChange }) {
  return (
    <div className="tabs">
      <button className={active === "adult" ? "tab active" : "tab"} onClick={() => onChange("adult")}> </button>
      <button className={active === "kids" ? "tab active" : "tab"} onClick={() => onChange("kids")}> </button>
    </div>
  );
}

export default function ClubCardsSection() {
  const [tab, setTab] = useState("adult");
  const plans = tab === "adult" ? ADULT_PLANS : KIDS_PLANS;

  return (
    <section
      id="club-cards"
      className="club-cards"
      style={{
        backgroundImage: `url(${bg})`,
        backgroundSize: "cover",
        backgroundPosition: "center",
        backgroundRepeat: "no-repeat",
        overflowX: "hidden"
      }}
    >
      <div className="container" style={{ maxWidth: 1200, margin: "0 auto", padding: "0 16px" }}>
        <h1 className="title" style={{ textAlign: "center" }}> </h1>
        <Tabs active={tab} onChange={setTab} />
        <DocumentLinks />
        <div className="grid">
          {plans.map((plan) => <PlanCard key={plan.id} plan={plan} />)}
        </div>
      </div>
    </section>
  );
}
