import React from "react";
import { Link } from "react-router-dom";

const ModalContent = ({ onClose }) => {
    const handleLinkClick = () => {
        onClose();
    }
    return (
        <div>
            <ul>
                <li><Link to="gym" onClick={handleLinkClick}> </Link></li>
                <li><Link to="boxing" onClick={handleLinkClick}> </Link></li>
                <li><Link to="studios" onClick={handleLinkClick}>    </Link></li>
                <li><Link to="pilates" onClick={handleLinkClick}>  </Link></li>
                <li><Link to="aquazone" onClick={handleLinkClick}>:    </Link></li>
                <li><Link to="spa" onClick={handleLinkClick}> SPA</Link></li>
                <li><Link to="table-tennis" onClick={handleLinkClick}> </Link></li>
            </ul>
        </div>
    );
};
export default ModalContent;