import React, { useMemo, useState } from "react";
import "./TrainersPage.css";

// -  
const FEMALE_IMG =
  "https://uniforce.ua/photos/cat/f/20250321_150737_16888-NR2P8F.jpg";
const MALE_IMG =
  "https://static5.depositphotos.com/1049184/509/i/450/depositphotos_5099757-stock-photo-the-man-with-no-face.jpg";

//    CTA ()
const POOL_IMG = "https://uniforce.ua/img/about/1.jpg";

// 
const TABS = [
  { id: "gym",    label: " " },
  { id: "aqua",   label: "" },
  { id: "groups", label: " " },
  { id: "kids",   label: " " },
  { id: "med",    label: "  " },
];

//   ()
const DATA = {
  gym: [
    { id: 1,  name: " ",     role: "-",          photo: MALE_IMG },
    { id: 2,  name: " ",    role: "-",          photo: MALE_IMG },
    { id: 3,  name: " ",    role: "-",      photo: FEMALE_IMG },
    { id: 4,  name: " ",         role: "-",          photo: MALE_IMG },
  ],
  aqua: [
    { id: 5,  name: " ",  role: "  ",   photo: MALE_IMG },
    { id: 6,  name: " ", role: "  ", photo: FEMALE_IMG },
    { id: 7,  name: " ",    role: "  ",   photo: MALE_IMG },
    { id: 8,  name: " ",        role: "  ", photo: FEMALE_IMG },
  ],
  groups: [
    { id: 9,  name: " ",     role: "  ", photo: FEMALE_IMG },
    { id: 10, name: " ",       role: "  ",   photo: MALE_IMG },
    { id: 11, name: " ",       role: "  ", photo: FEMALE_IMG },
    { id: 12, name: " ",        role: "  ",   photo: MALE_IMG },
  ],
  kids: [
    { id: 13, name: " ",   role: " ",        photo: FEMALE_IMG },
    { id: 14, name: " ",   role: " ",         photo: MALE_IMG },
    { id: 15, name: " ",       role: " ",        photo: FEMALE_IMG },
    { id: 16, name: " ",         role: " ",         photo: MALE_IMG },
  ],
  med: [
    { id: 17, name: " ",     role: "-",       photo: FEMALE_IMG },
    { id: 18, name: " ",     role: "",               photo: MALE_IMG },
    { id: 19, name: " ",       role: "-",       photo: FEMALE_IMG },
    { id: 20, name: " ",      role: "",               photo: MALE_IMG },
  ],
};

function Tabs({ active, onChange }) {
  return (
    <div className="tabs">
      {TABS.map((t) => (
        <button
          key={t.id}
          type="button"
          className={"tab" + (active === t.id ? " active" : "")}
          onClick={() => onChange(t.id)}
        >
          {t.label}
        </button>
      ))}
    </div>
  );
}

function TrainerCard({ name, role, photo }) {
  return (
    <article className="coach">
      <img className="coach-img" src={photo} alt={name} />
      <div className="coach-content">
        <h3 className="coach-name">{name}</h3>
        <p className="coach-role">{role}</p>
        <button className="coach-btn" type="button"></button>
      </div>
    </article>
  );
}

export default function TrainersPage() {
  const [tab, setTab] = useState("gym");
  const items = useMemo(() => DATA[tab] || [], [tab]);

  return (
    <>
      {/*  :   fixed background +  overlay */}
      <section className="trainers-page">
        <div className="trainers-overlay">
          <Tabs active={tab} onChange={setTab} />
          <div className="cards">
            {items.map((t) => (
              <TrainerCard key={t.id} {...t} />
            ))}
          </div>
        </div>
      </section>

      {/*  CTA:   fixed background,  overlay +   */}
      <section className="cta">
        <div className="cta-inner">
          <div className="cta-copy">
            <h2 className="cta-title">
                <br /> 
            </h2>

            <label className="cta-input-wrap">
              <span className="cta-prefix">+38</span>
              <input
                type="tel"
                className="cta-input"
                placeholder="(0_) ___ __ __"
                aria-label=" "
              />
            </label>

            <button type="button" className="cta-btn">
               
            </button>
          </div>

          {/*       */}
          <figure className="cta-figure" aria-label=" UNI FORCE">
            <img className="cta-img" src={POOL_IMG} alt="UNI FORCE pool" />
          </figure>
        </div>
      </section>
    </>
  );
}
